@import "../../../master.scss";

.figure-square-wrapper {
  margin-top: 20px;
  h6 {
    color: $text-base-color;
  }
  p {
    color: #b7b7b7;
  }

  .figure-image {
    position: relative;
    a {
      opacity: 0;
      top: 0;
      left: 0;
      right: 0;
      bottom: 8px;
      position: absolute;
      background-color: rgba(0, 0, 0, 0.7);
      transition: opacity 0.3s;
      .img {
        margin: auto;
      }
    }
    a:hover {
      opacity: 1;
    }
  }
}
