@import "../../../master.scss";
.carousel-wrapper {
  font-family: "Lora";
  margin-top: 89px;
  .carousel {
    background-color: #f1e2db;
    height: 450px;
    h1 {
      font-size: 48px;
      font-weight: 700;
      color: $text-base-color;
    }
    p {
      font-weight: 300;
      font-size: 18px;
      color: $text-base-color;
      font-family: "Montserrat";
      margin-top: 20px;
    }
    .btn-slide {
      margin-top: 20px;
      background-color: $text-base-color;
      font-weight: 600;
      font-family: "Montserrat";
    }
  }
}
