@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;500;600;700&family=Montserrat:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Montserrat" !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: "#2c4874" !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-wrapper .navbar{border-bottom:1px solid #f1f1f1;background-color:#fff;border-top:10px}.navbar-wrapper .navbar .nav-link{color:#2c4874 !important}.navbar-wrapper .navbar .nav-link .link{margin-right:24px}.navbar-wrapper .navbar .active{font-weight:600 !important}.navbar-wrapper .nav-cart{color:#2c4874;text-decoration:none}.navbar-wrapper p{font-weight:600;font-size:20px;color:#2c4874}.navbar-wrapper a{text-decoration:none}

.carousel-wrapper{font-family:"Lora";margin-top:89px}.carousel-wrapper .carousel{background-color:#f1e2db;height:450px}.carousel-wrapper .carousel h1{font-size:48px;font-weight:700;color:#2c4874}.carousel-wrapper .carousel p{font-weight:300;font-size:18px;color:#2c4874;font-family:"Montserrat";margin-top:20px}.carousel-wrapper .carousel .btn-slide{margin-top:20px;background-color:#2c4874;font-weight:600;font-family:"Montserrat"}

.figure-square-wrapper{margin-top:20px}.figure-square-wrapper h6{color:#2c4874}.figure-square-wrapper p{color:#b7b7b7}.figure-square-wrapper .figure-image{position:relative}.figure-square-wrapper .figure-image a{opacity:0;top:0;left:0;right:0;bottom:8px;position:absolute;background-color:rgba(0,0,0,0.7);transition:opacity 0.3s}.figure-square-wrapper .figure-image a .img{margin:auto}.figure-square-wrapper .figure-image a:hover{opacity:1}

.header-section h3{font-family:Lora;font-weight:bold;color:#2c4874}.header-section p{font-weight:300;color:#2c4874}

.feature-wrapper{margin-top:30px}

.figure-wrapper{margin-top:20px}.figure-wrapper h6{color:#2c4874}.figure-wrapper p{color:#b7b7b7}.figure-wrapper .img-wrapper{width:110px;height:110px}.figure-wrapper .img-wrapper:hover{border:1px solid #2c4874}

.designer-wrapper{margin-top:50px}

.footer-wrapper{margin-top:100px}.footer-wrapper .footer-nav a{color:#2c4874 !important}

.breadcumb-wrapper{margin-top:110px;color:#2c4874}

.single-product-wrapper{margin-top:23px}.single-product-wrapper .single-thumb-wrapper{max-width:380px;padding:5px;margin:auto;box-shadow:0 4px 15px rgba(0,0,0,0.1);transform:translateY(-55px)}.single-product-wrapper .single-thumb-wrapper a:hover{border:1px solid #2c4874}.single-product-wrapper .counter{width:120px;color:#2c4874}.single-product-wrapper .btn-add-cart{background-color:#2c4874;border:none;font-weight:600}.single-product-wrapper .btn-wishlist{background-color:#f6f7fb;color:#adadad;border:none}.single-product-wrapper h4{color:#2c4874;margin-top:10px}.single-product-wrapper h5{color:#adadad;font-weight:300}.single-product-wrapper hr{width:250px;margin-left:0;border:1px solid #f4f4f4;margin-top:30px}.single-product-wrapper .designer-wrapper h6{font-weight:300;font-size:14px;color:#2c4874;margin-bottom:20px}.single-product-wrapper .designer-wrapper .name{font-weight:600;font-size:18px;color:#2c4874}.single-product-wrapper .designer-wrapper .followers{font-weight:300;font-size:14px;color:#adadad}


