@import "../../../master.scss";

.navbar-wrapper {
  .navbar {
    border-bottom: 1px solid rgb(241, 241, 241);
    background-color: #fff;
    border-top: 10px;
    .nav-link {
      color: #2c4874 !important;
      .link {
        margin-right: 24px;
      }
    }
    .active {
      font-weight: 600 !important;
    }
  }
  .nav-cart {
    color: $text-base-color;
    text-decoration: none;
  }
  p {
    font-weight: 600;
    font-size: 20px;
    color: $text-base-color;
  }

  a {
    text-decoration: none;
  }
}
