@import "../../master.scss";
.single-product-wrapper {
  margin-top: 23px;
  .single-thumb-wrapper {
    max-width: 380px;
    padding: 5px;
    margin: auto;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transform: translateY(-55px);
    a:hover {
      border: 1px solid $text-base-color;
    }
  }
  .counter {
    width: 120px;
    color: $text-base-color;
  }
  .btn-add-cart {
    background-color: $text-base-color;
    border: none;
    font-weight: 600;
  }

  .btn-wishlist {
    background-color: #f6f7fb;
    color: #adadad;
    border: none;
  }

  h4 {
    color: $text-base-color;
    margin-top: 10px;
  }

  h5 {
    color: #adadad;
    font-weight: 300;
  }

  hr {
    width: 250px;
    margin-left: 0;
    border: 1px solid #f4f4f4;
    margin-top: 30px;
  }

  .designer-wrapper {
    h6 {
      font-weight: 300;
      font-size: 14px;
      color: $text-base-color;
      margin-bottom: 20px;
    }
    .name {
      font-weight: 600;
      font-size: 18px;
      color: $text-base-color;
    }
    .followers {
      font-weight: 300;
      font-size: 14px;
      color: #adadad;
    }
  }
}
